import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { User } from '../../models/user';
import * as firebase from 'firebase';


@IonicPage()
@Component({
  selector: 'page-register',
  templateUrl: 'register.html',
})
export class RegisterPage {

  user = {} as User;
  retypePwd = "";

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              private toastCtrl: ToastController) {
  }

  async register(user: User){
    try{
      if (user.password.length < 6 || user.password.length > 18){
        throw {code: "pwd/length"};
      }
      if (user.password != this.retypePwd){
        throw {code: "pwd/match"};
      }
      const result = await firebase.auth().createUserWithEmailAndPassword(user.email, user.password)
      .then((res) => {
        let user = firebase.auth().currentUser;
        user.sendEmailVerification();
        this.initialUserDoc(user.uid, user.email);
        firebase.auth().signOut();
        this.navCtrl.pop();
      });
    }
    catch(e){
      var err_msg = "";
      switch (e.code){
        case('pwd/match'): {
          err_msg = "Re-Type password didn't match!";
          this.user.password = this.retypePwd = "";
          break;
        }
        case('auth/email-already-in-use'): {
          err_msg = "Email already registered!";
          break;
        }
        case('auth/invalid-email'): {
          err_msg = "Please Using an email to register!";
          break;
        }
        case('pwd/length'): {
          err_msg = "Password should be 6-18 digits!";
          break;
        }
        default:{
          err_msg = e.code;
          break;
        }
      }
      let err = this.toastCtrl.create({
        message: err_msg,
        duration: 3000,
        position: 'bottom'
      })
      err.present();
    }
  }

  initialUserDoc(userId, userEmail){
    let cu = {} as User;
    cu.email = userEmail;
    cu.username = "";
    cu.firstName = "";
    cu.lastName = "";
    cu.eventList = [];
    cu.friendList = [];
    cu.blockedUsers = [];
    cu.location = new firebase.firestore.GeoPoint(0,0);
    firebase.firestore().collection('Users').doc(userId).set(cu);
  }

  ionViewDidLoad() {
    
  }

}


